import React, { useState, useEffect } from "react";
import { useHistory } from "react-router-dom";
import Grid from "@material-ui/core/Grid";
import news from "../../../../assets/image/news/news.png";
import NewsComponent from "../../../../components/News/News.component";

export default function News() {
  const history = useHistory();
  const arr = [1, 2, 3];

  const handleClick = () => {
    history.push("/danh-muc/bai-viet/thuong-hieu-aqua--qua-trinh-hinh-thanh-0");
  };
  const listsNews = arr.map((e, index) => {
    return (
      <Grid item lg={4} md={4} xs={12} onClick={handleClick}>
        <NewsComponent
          img={news}
          title="Trao tng 25 my lc nc v 10.000 khu trang y t trn a bn huyn Hu Lc"
          description="Sng ngy 26/10/2021 Cng ty CP u t x l nc sch Aqua Vit Nam tip tc trao tng cho Hi ph n huyn, HPN cc..."
        />
      </Grid>
    );
  });

  return (
    <Grid className="news">
      <div className="header-title">
        <span>Tin tc</span>
      </div>
      <div className="mt-3">
        <Grid container spacing={3}>
          {listsNews}
        </Grid>
      </div>
    </Grid>
  );
}
